<script lang="ts">
    export let word: string;
    export let syllableIndex: number;

    $: wordParts = getWordParts(word, syllableIndex);

    function getWordParts(currentWord, currentSyllableIndex) {
        
        if (!currentWord) {
            return null;
        }
        let indexOfNextBreak = currentWord.indexOf(' ', syllableIndex + 1);
        indexOfNextBreak = indexOfNextBreak > -1 ? indexOfNextBreak : currentWord.length;
        
        const result = {
            pre: currentWord.substring(0, currentSyllableIndex),
            text: currentWord.substring(currentSyllableIndex, indexOfNextBreak),
            post: currentWord.substring(indexOfNextBreak, currentWord.length),
        };
        return result;
    }
</script>

<style>
    .container {
        display: grid;
        place-items: center;
        width: 100%;
    }

    .container__item {

    }

    .word {
        font-size: clamp(3rem, 20vw, 20rem);
    }

    .word__part {
        color: lightgray;
    }

    .word__part--syllable {
        color: black;
    }
</style>

{#if wordParts}
    <div class="container">
        <span class="container__item word"><!--
        -->{#if wordParts.pre}<!--
            --><span class="word__part">{ wordParts.pre.replace(/ /g, '') }</span><!--
        -->{/if}<!--
        -->{#if wordParts.text}<!--
            --><span class="word__part word__part--syllable">{ wordParts.text.replace(/ /g, '') }</span><!--
        -->{/if}<!--
        -->{#if wordParts.post}<!--
            --><span class="word__part">{ wordParts.post.replace(/ /g, '') }</span><!--
        -->{/if}<!--
    --></span>
    </div>
{/if}