<script lang="ts">
	import { onMount, onDestroy } from 'svelte';
	import { Mode } from './mode';
	import TextInput from './TextInput.svelte';
	import Word from './Word.svelte';
	import * as Hammer from 'hammerjs';

	let text: string;
	let words: string[] = [];
	let wordIndex = 0;
	let syllableIndex = 0;
	let mode: Mode = Mode.Display;
	let manager: Hammer.Manager;

	onMount(() => {
		// todo load from localStorage
		text = 'Ma ma\nmá\nE mu.\nE mu\nmá\nma mu.';
		words = split(text);

		setupTouchEvents();
	}); 

	onDestroy(() => {
		if (manager) {
			manager.off('swipe', handleSwipe);
		}
	})

	function setupTouchEvents() {
		const mainSection = document.getElementById('main-section');
		
		manager = new Hammer.Manager(mainSection);
		manager.add(new Hammer.Swipe({ direction: Hammer.DIRECTION_HORIZONTAL }));

		manager.on('swipe', handleSwipe);
	}

	function handleSwipe(event: HammerInput) {
		if(event.direction === Hammer.DIRECTION_LEFT) {
			moveToNext();
		} else if (event.direction === Hammer.DIRECTION_RIGHT) {
			moveToPrevious();
		}
	}

	function moveToNext() {
		const nextIndex = words[wordIndex].indexOf(' ', syllableIndex + 1);

		if (nextIndex > -1) {
			syllableIndex = nextIndex;
			return;
		}

		if (words.length > wordIndex + 1) {
			wordIndex++;
			syllableIndex = 0;
		}
	}

	function moveToPrevious() {
		if (syllableIndex === 0 && wordIndex > 0) {
			wordIndex--;
			
			const lastIndexOfBreak = words[wordIndex].lastIndexOf(' ');
			syllableIndex = lastIndexOfBreak > -1 ? lastIndexOfBreak : 0;
			return;
		}

		const lastIndexOfBreak = words[wordIndex].substring(0, syllableIndex).lastIndexOf(' ');
		syllableIndex = lastIndexOfBreak > -1 ? lastIndexOfBreak : 0;
	}

	function moveToStart() {
		wordIndex = 0;
		syllableIndex = 0;
	}

	function moveToEnd() {
		wordIndex = words.length - 1;
		syllableIndex = words[wordIndex].lastIndexOf(' ');
	}

	function onTextChange(event: {detail: string}) {
		text = event.detail;
		words = split(text);

		if (wordIndex > words.length) {
			wordIndex = words.length - 1;
		}
	}

	function onKeydown(event: KeyboardEvent) {

		switch(event.key) {
			case 'ArrowLeft':
				event.preventDefault();
				if(event.shiftKey) {
					moveToStart()
				} else {
					moveToPrevious();
				}
				break;
			case 'ArrowRight':
				event.preventDefault();
				if(event.shiftKey) {
					moveToEnd()
				} else {
					moveToNext();
				}
				break;
			case ' ':
				if (mode === Mode.Display) {
					changeMode();
				}
				break;
			case 'Escape':
				if (mode === Mode.Edit) {
					changeMode();
				}
				break;
		}
	}

	function changeMode() {
		if (mode === Mode.Display) {
			mode = Mode.Edit;
		} else {
			mode = Mode.Display;
		}
	}

	function split(input: string) {
		return input.split('\n');
	}
</script>

<main class="section">
	<div 
		id="main-section"
		class="section__item section__item--primary"
	>
		{#if mode === Mode.Display}
			<Word 
				word={words[wordIndex]} 
				{syllableIndex}
			/>		
		{:else}
			<div class="edit-block">
				<div class="edit-block__row">	
					<TextInput 
						{text}
						on:valueChange={onTextChange}
					/>
				</div>
			</div>
		{/if}
	</div>

	<div class="section-item button-strip">
		<button 
			on:click={changeMode}
			class="button-strip__button"
		>
			{ mode === Mode.Display ? 'Edit' : 'Display' }
		</button>
	</div>
</main>

<svelte:window on:keydown={onKeydown}/>

<style>
	.section {
		display: flex;
		flex-direction: column;
		align-items: stretch;
		height: 100%;
	}

	.section__item {
		display: flex;
		flex-grow: 0;
	}

	.section__item--primary {
		flex-grow: 1;
	}

	.button-strip {
		display: flex;
		width: 100%;
		flex-direction: column;
		align-items: flex-end;
	}

	.button-strip__button {
	}

	.edit-block {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 100%;
		align-items: center;
		justify-content: center;
	}
</style>